*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
}

html,
body {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}
body {
  text-align: center;
  font-family: "Righteous", serif;
  color: white;
  width: 100vw;
  height: 100vh;
}
body:before {
  content: "";
  background-image: url("/static/media/The Picture of Dorian Gray - The Preface.4b92fa7b.jpeg");
  background-size: cover;
  background-position: left 0 bottom 75%;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.38;
}


